.project-header-header {
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  max-width: 1400px;
  align-items: center;
  padding-top: 20px;
  padding-left: 48px;
  padding-right: 48px;
  justify-content: space-between;
  background-color: var(--dl-color-gray-white);
}
.project-header-navlink {
  font-size: 38px;
  font-family: "Send Flowers";
  text-decoration: none;
}
.project-header-burger-menu {
  display: none;
}
.project-header-icon {
  width: 16px;
  height: 16px;
  display: flex;
}
.project-header-mobile-menu {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  display: none;
  padding: 32px;
  z-index: 100;
  position: absolute;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
}
.project-header-nav {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.project-header-container {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 48px;
  justify-content: space-between;
}
.project-header-image {
  height: 2rem;
}
.project-header-close-mobile-menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
.project-header-icon02 {
  width: 16px;
  height: 16px;
}
.project-header-icon04 {
  width: 16px;
  height: 16px;
  margin-right: 32px;
}
.project-header-icon06 {
  width: 16px;
  height: 16px;
  margin-right: 32px;
}
.project-header-icon08 {
  width: 16px;
  height: 16px;
}
@media(max-width: 767px) {
  .project-header-header {
    padding-left: 32px;
    padding-right: 32px;
  }
  .project-header-burger-menu {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
@media(max-width: 479px) {
  .project-header-header {
    padding: var(--dl-space-space-unit);
  }
  .project-header-mobile-menu {
    padding: 16px;
  }
}
