/* for the whole card */
.project-card-container {
  width: 300px;
  height: 372px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.project-card-image {
  width: 300px;
  height: 210px;
  object-fit: cover;
}
/* for the sub card (texts) */
.project-card-container1 {
  width: 269px;
  height: 164px;
  display: flex;
  align-items: center;
  padding-top: 10px;
  flex-direction: column;
}
.project-card-text {
  color: var(--dl-color-gray-black);
  font-size: 20px;
  font-style: normal;
  text-align: center;
  font-family: "Cinzel";
  font-weight: 700;
}
.project-card-text1 {
  color: var(--dl-color-gray-black);
  font-size: 16px;
  max-width: 270px;
  text-align: left;
  font-family: "Comic Sans MS";
  margin-bottom: var(--dl-space-space-halfunit);
}
.project-card-root-class-name {
  margin-bottom: 0px;
}
@media(max-width: 767px) {
  .project-card-container {
    width: 200px;
  }
}
@media(max-width: 479px) {
  .project-card-container {
    width: 300px;
  }
}
