.question-mark-page-container {
  width: 100%;
  height: auto;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
}
.question-mark-page-header {
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  max-width: 1400px;
  align-items: center;
  padding-top: 20px;
  padding-left: 48px;
  padding-right: 48px;
  justify-content: space-between;
  background-color: var(--dl-color-gray-white);
}
.question-mark-page-navlink {
  font-size: 38px;
  font-family: "Send Flowers";
  text-decoration: none;
}
.question-mark-page-burger-menu {
  display: none;
}
.question-mark-page-icon {
  width: 16px;
  height: 16px;
  display: flex;
}
.question-mark-page-mobile-menu {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100vh;
  display: none;
  padding: 32px;
  z-index: 100;
  position: absolute;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
}
.question-mark-page-nav {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.question-mark-page-container1 {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 48px;
  justify-content: space-between;
}
.question-mark-page-image {
  height: 2rem;
}
.question-mark-page-close-mobile-menu {
  display: flex;
  align-items: center;
  justify-content: center;
}
.question-mark-page-icon02 {
  width: 16px;
  height: 16px;
}
.question-mark-page-icon04 {
  width: 16px;
  height: 16px;
  margin-right: 32px;
}
.question-mark-page-icon06 {
  width: 16px;
  height: 16px;
  margin-right: 32px;
}
.question-mark-page-icon08 {
  width: 16px;
  height: 16px;
}
.question-mark-page-footer {
  width: 100%;
  height: 78px;
  display: flex;
  position: relative;
  max-width: 1400px;
  align-items: center;
  padding-top: 32px;
  padding-left: 48px;
  padding-right: 48px;
  padding-bottom: 32px;
  justify-content: space-between;
}
.question-mark-page-text {
  font-size: 28px;
  font-family: "Send Flowers";
}
.question-mark-page-icon-group1 {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.question-mark-page-link {
  display: contents;
}
.question-mark-page-icon10 {
  width: 24px;
  height: 24px;
  text-decoration: none;
}
@media(max-width: 767px) {
  .question-mark-page-header {
    padding-left: 32px;
    padding-right: 32px;
  }
  .question-mark-page-burger-menu {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .question-mark-page-footer {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media(max-width: 479px) {
  .question-mark-page-header {
    padding: var(--dl-space-space-unit);
  }
  .question-mark-page-mobile-menu {
    padding: 16px;
  }
  .question-mark-page-footer {
    padding: var(--dl-space-space-unit);
    flex-direction: column;
  }
}
