.signature-footer-footer {
  width: 100%;
  height: 78px;
  display: flex;
  position: relative;
  max-width: 1400px;
  align-items: center;
  padding-top: 32px;
  padding-left: 48px;
  padding-right: 48px;
  padding-bottom: 32px;
  justify-content: space-between;
}
.signature-footer-navlink {
  font-size: 28px;
  font-family: "Send Flowers";
  text-decoration: none;
}
.signature-footer-root-class-name {
  height: auto;
}
@media(max-width: 767px) {
  .signature-footer-footer {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media(max-width: 479px) {
  .signature-footer-footer {
    padding: var(--dl-space-space-unit);
    flex-direction: column;
  }
}
