.camera-project-page-container {
  width: 100%;
  display: flex;
  overflow: auto;
  min-height: 100vh;
  align-items: center;
  flex-direction: column;
}
.camera-project-page-hero {
  width: 100%;
  height: auto;
  display: flex;
  position: relative;
  min-height: 80vh;
  align-items: center;
  padding-top: 0px;
  padding-left: 48px;
  padding-right: 48px;
  flex-direction: column;
  padding-bottom: 0px;
  justify-content: flex-start;
}
.camera-project-page-text {
  font-size: 3rem;
  font-style: normal;
  font-family: "Cinzel Decorative";
  font-weight: 400;
  line-height: 1.55;
  margin-bottom: 0.25em;
}
.camera-project-page-image {
  width: 45rem;
  margin-top: 0px;
  margin-bottom: var(--dl-space-space-halfunit);
}
.camera-project-page-container1 {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.camera-project-page-text01 {
  font-size: 26px;
  margin-top: var(--dl-space-space-halfunit);
  text-align: justify;
  font-family: "Cormorant";
  font-weight: 400;
  line-height: 1.55;
  padding-left: 48px;
  margin-bottom: var(--dl-space-space-halfunit);
  padding-right: 48px;
  text-transform: none;
  text-decoration: none;
}
.camera-project-page-container2 {
  flex: 0 0 auto;
  width: auto;
  height: auto;
  display: flex;
  z-index: 100;
  margin-top: var(--dl-space-space-unit);
  align-items: flex-start;
  border-color: var(--dl-color-gray-black);
  border-width: 0px;
  margin-right: 0px;
  border-radius: var(--dl-radius-radius-radius4);
  padding-right: 0px;
  justify-content: center;
}
.camera-project-page-image1 {
  width: 200px;
  object-fit: cover;
  margin-right: var(--dl-space-space-unit);
}
.camera-project-page-image2 {
  width: 200px;
  object-fit: cover;
  margin-right: var(--dl-space-space-unit);
}
.camera-project-page-image3 {
  width: 200px;
  object-fit: cover;
  margin-right: var(--dl-space-space-unit);
}
.camera-project-page-image4 {
  width: 200px;
  z-index: 100;
  object-fit: cover;
  border-color: var(--dl-color-gray-black);
  border-width: 1px;
  border-radius: 0px;
}
@media(max-width: 991px) {
  .camera-project-page-hero {
    flex-direction: column;
  }
  .camera-project-page-text {
    text-align: center;
  }
  .camera-project-page-image {
    width: 80%;
  }
  .camera-project-page-container1 {
    align-items: center;
    margin-right: 0px;
    margin-bottom: 32px;
  }
}
@media(max-width: 767px) {
  .camera-project-page-hero {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media(max-width: 479px) {
  .camera-project-page-hero {
    padding-top: 32px;
    padding-left: var(--dl-space-space-unit);
    padding-right: var(--dl-space-space-unit);
    padding-bottom: 32px;
  }
  .camera-project-page-container1 {
    margin-bottom: var(--dl-space-space-unit);
  }
}
